.social {
  display: flex;

  @media (max-width: $breakpoint-mobile) {
    min-height: 23px;
    width: 180px;
    margin-right: auto;
  }

  & ul {
    @include no-list;
    display: flex;
    align-content: flex-end;
    flex-direction: column;
    flex-wrap: wrap;
    width: 115px;
    margin-left: 30px;

    @media (max-width: $breakpoint-mobile) {
      flex-direction: row;
      margin-left: 0;
      width: 180px;
    }
  }

  & li {
    display: flex;
    margin-bottom: 21px;

    @media (max-width: $breakpoint-mobile) {
      margin-right: 25px;
      margin-bottom: 10px;
    }
  }

  & a {
    position: relative;
    color: $white;

    @media (max-width: $breakpoint-mobile) {
      width: 20px;
      height: 21px;
    }

    &:hover {
      opacity: 0.5;
    }

    &:active {
      opacity: 1;
    }
  }

  & span {
    @media (max-width: $breakpoint-mobile) {
      display: none;
    }
  } 
}

.social__icon {
  position: absolute;
  top: 3px;
  left: -28px;
  fill: white;

  @media (max-width: $breakpoint-mobile) {
    width: 12px;
    height: 21px;
    top: 3px;
    left: 1px;
  
    &--inst {
      width: 20px;
      height: 21px;
      left: -3px;
    }
  
    &--pintr {
      width: 20px;
      height: 21px;
      left: 4px;
    }
  
    &--tw {
      width: 20px;
      height: 18px;
      top: 5px;
      left: 6px;
    }
  }
}

